<template>
  <div id="login">
      <div class="login-form">
        <div class="login-inner">
            <h2>
              <p>通过邮箱账号找回</p>
              <p class="f14">FIND</p>
            </h2>
            <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="80px" class="demo-ruleForm">
              <el-form-item label="用户账号" prop="number">
                <div class="sendcode">
                    <el-input  v-model="ruleForm.number"></el-input>
                    <el-button type="warning" :loading="isLoad" @click="sendcode">发送验证码</el-button>
                </div>
              </el-form-item>
              <el-form-item label="验证码" prop="sencode">
                <el-input v-model="ruleForm.sencode"></el-input>
              </el-form-item>
              <el-form-item label="新密码" prop="newpass">
                <el-input v-model="ruleForm.newpass" type="password"></el-input>
              </el-form-item>
              <div class="link">
                <router-link to="/Login/tFindAccount">通过手机号找回</router-link>
              </div>
              <el-form-item>
                <el-button type="primary" @click="submitForm('ruleForm')">找回</el-button>
              </el-form-item>
            </el-form>
        </div>
      </div>
  </div>
</template>

<script>
export default {
  data(){
    return{
      isLoad:false,
      ruleForm:{
        number:'',
        sencode:'',
        newpass:''
      },
      rules:{
        number:[
          { required:true,message:'请输入要找回的账号',trigger:'blur'},
        ],
        sencode:[
          { required:true,message:'请输入验证码',trigger:'blur'}
        ],
        newpass:[
          { required:true,message:'请输入新密码',trigger:'blur'},
          { min:6,max:16,message:'密码长度为6至16',trigger:'blur'}
        ]
      }
    }
  },
  methods:{
    submitForm(formName){
      let that=this;
      this.$refs[formName].validate((valid)=>{
          if(valid){
            that.$api.findemcode(that.ruleForm.number,{
              code:that.ruleForm.sencode,
              new_pass:that.ruleForm.newpass
            }).then(res=>{
              if(res.err_code===100){
                that.$message('修改成功');
                that.$router.push('/');
              }else if(res.err_code === 101){
                that.$message.error('验证码错误');
              }
            })
          }
      })
    },
    sendcode(){
      let that=this;
      that.isLoad=true;
      this.$api.findpassem(this.ruleForm.number).then(res=>{
        if(res.err_code === 100){
          that.$message('验证码发送成功,请注意查收');
          that.isLoad=false;
        }
      })
    },
    resetForm(formName){
      this.$refs[formName].resetFields();
    }
  }
}
</script>

<style lang="scss" scoped>
    #login{
      width: 800px;
      padding: 50px 0;
      margin: auto;

      .login-form{
          margin: 0 auto;
          width: 420px;
          display: flex;
          justify-content: center;
          align-items: center;
          box-sizing: border-box;
          padding: 10px;
          background: #fff;
          
          .login-inner{
            width: 100%;
            height: 100%;
            border: 1.2px solid #4579c7;
            padding: 10px 26px;
            box-sizing: border-box;

            h2{
              font-weight: 500;
              border-bottom: 1px dashed #333;
              text-align: left;
              p.f14{
                font-weight: 800;
                font-size: 14px;
                color: #d5d5d5;
              }
            }
          }
          .el-form{
            margin-top: 10px;
          }
          .toResister{
            text-align: right;
            a{
              font-size: 14px;
            }
          }
      }
      .link{
        text-align: right;

        &:hover{
          text-decoration: underline;
        }
      }
      .sendcode{
            display: flex;
            .el-button{
                margin: 0 5px;
            }
        }
        .findtype{
            font-size: 14px;
            font-weight: 800;
            text-align: right;
            
            &:hover{
                color: #236bb5;
                cursor: pointer;
                text-decoration: underline;
            }
        }
    }
</style>